<script>
import Chart from "chart.js";
// eslint-disable-next-line no-unused-vars
import { ref, watch } from "vue";

import { SessionTable, SessionsList } from "@/components";
import { mainStore } from "@/system/mainStore";
import VInput from "../../components/VInput";
import { THUMBNAIL_BASE_URL } from "@/system/config";

export default {
  name: "Charts",
  components: {
    SessionTable,
    SessionsList,
    VInput,
  },

  methods: {
    createChart(chartId, chartData) {
      const ctx = document.getElementById(chartId);

      return new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options,
      });
    },
    // eslint-disable-next-line no-unused-vars
    pSelectionSwitch: async function(id) {
      mainStore.switchParticipantSelection(id);
    },

    changeMode() {
      if (this.modeValue === "multisession") {
        this.modeValue = "singlesession";
      } else {
        this.modeValue = "multisession";
      }
    },

    displayModal(modalName) {
      this.activeModalName = modalName;
    },

    closeModal() {
      this.activeModalName = null;
    },

    setRequestData() {
      this.updateFilterData();
      console.log(this.requestData);
      if (!this.isFilterUnchecked) mainStore.setFilteredSessions(this.requestData);
      this.renderMultSessionDataCharts();
    },

    renderMultSessionDataCharts() {
      this.isMultSessionDataShown = true;
    },

    setupDate(date, dateName) {
      this.changeSelected(null);
      switch (dateName) {
        case "startDate":
          this.defaultRequestData.startDate = date.split("-").map((el) => Number(el));
          this.defaultRequestData.startDate[1] = this.defaultRequestData.startDate[1] - 1;
          break;
        case "endDate":
          this.defaultRequestData.endDate = date.split("-").map((el) => Number(el));
          this.defaultRequestData.endDate[1] = this.defaultRequestData.endDate[1] - 1;
          break;
        default:
          return;
      }
    },

    changeSelected(btn) {
      if (!btn) {
        this.dateFilterButtons.forEach((btn) => {
          btn.selected = false;
        });
      } else {
        this.dateFilterButtons.forEach((btn) => {
          btn.selected = false;
        });
        btn.selected = true;
      }
    },

    lastDaysDate(start, end, btn, shouldDataUpdate) {
      this.changeSelected(btn);
      this.startDate = false;
      this.endDate = false;
      function formatDate(date) {
        if (date < 10) {
          date = "0" + date;
        }
        return date;
      }

      let startDay = formatDate(start.getDate());
      let endDay = formatDate(end.getDate());

      let startMonth = formatDate(start.getMonth() + 1);
      let endMonth = formatDate(end.getMonth() + 1);

      let startDate = (start.getFullYear() + "-" + startMonth + "-" + startDay)
        .split("-")
        .map((el) => Number(el));
      let endDate = (end.getFullYear() + "-" + endMonth + "-" + endDay)
        .split("-")
        .map((el) => Number(el));

      this.defaultRequestData.startDate = startDate;
      this.defaultRequestData.endDate = endDate;

      if (shouldDataUpdate) {
        this.setRequestData();
      }
    },

    selectAll(name) {
      for (let filter in this.filters) {
        if (filter === name) {
          this.filters[filter].filterData.forEach((item) => {
            item.checked = true;
          });
        }
      }
      this.setRequestData();
    },

    toggleChecked(checked, filter, shouldDataUpdate) {
      filter.checked = !checked;
      filter.checked ? (this.isFilterUnchecked = false) : (this.isFilterUnchecked = true);
      if (shouldDataUpdate) {
        this.setRequestData();
      }
    },

    toggleAggregatedMode() {
      this.aggregatedMode = !this.aggregatedMode;
    },

    copyRequestObj(obj) {
      return {
        ...obj,
        age: [...obj.age],
        ethnicity: [...obj.ethnicity],
        gender: [...obj.gender],
        charts: [...obj.charts],
      };
    },

    updateFilterData() {
      this.requestData = this.copyRequestObj(this.defaultRequestData);
      this.filters.charts.filterData.forEach((item) => {
        item.checked ? this.requestData.charts.push(+item.value) : (this.requestData.charts = []);
      });

      for (let key in this.filters) {
        let checkedArray = this.filters[key].filterData.filter((item) => item.checked);
        if (!checkedArray.length) {
          if (key === "age") {
            this.requestData[key] = this.filters[key].filterData.map((item) => item.value);
          } else {
            this.requestData[key] = this.filters[key].filterData.map((item) => +item.value);
          }
        } else {
          this.requestData[key].splice(0, this.requestData[key].length);
          this.filters[key].filterData.forEach((item) => {
            if (key === "age") {
              item.checked ? this.requestData[key].push(item.value) : false;
            } else {
              item.checked ? this.requestData[key].push(+item.value) : false;
            }
          });
        }
      }
    },
    getDate(date, dateName) {
      if (this.startDate || this.endDate) {
        this.setupDate(date.target.value, dateName);
      } else if (!this.startDate || !this.endDate) {
        if (dateName === "startDate") {
          this.startDate = new Date();
          this.setupDate(date.target.value, dateName);
        } else if (dateName === "endDate") {
          this.endDate = new Date();
          this.setupDate(date.target.value, dateName);
        }
      }
    },

    clearGraphs() {
      mainStore.clearMultData();
      for (let key in this.filters) {
        this.filters[key].filterData.forEach((item) => {
          item.checked = false;
        });
      }
    },
  },

  data() {
    return {
      THUMBNAIL_BASE_URL,

      pieChartData: mainStore.speakingChart,
      sessions: mainStore.sessions,
      selected: mainStore.currentSession,
      isLoadingState: mainStore.sessionDataIsLoading,
      isLoadingSessions: mainStore.sessionsAreLoadings,
      sessionParticipants: mainStore.sessionParticipants,
      speakingChart: mainStore.speakingChart,
      switchAggregatedMode: mainStore.switchAggregatedMode,
      aggregatedMode: mainStore.aggregatedMode,

      modeValue: "singlesession",

      modals: [
        "user-emotions-chart-modal",
        "engagement-chart-modal",
        "attention-chart-modal",
        "speaking-chart-modal",
        "emotions-chart-modal",
      ],

      isFilterUnchecked: false,

      startDate: new Date(),
      endDate: new Date(),
      activeModalName: null,

      isMultSessionDataShown: false,

      filters: {
        age: {
          filterName: "age",
          filterData: [
            { name: "0-20", value: "0-20", checked: false, id: "0-20" },
            { name: "21-35", value: "21-35", checked: false, id: "21-35" },
            { name: "36-50", value: "36-50", checked: false, id: "36-50" },
            { name: "50+", value: "51-1000", checked: false, id: "50+" },
          ],
        },
        ethnicity: {
          filterName: "ethnicity",
          filterData: [
            { name: "Asian", value: "3", checked: false, id: "asian" },
            { name: "Latino", value: "4", checked: false, id: "latino" },
            { name: "Black", value: "1", checked: false, id: "black" },
            { name: "Middle eastern", value: "5", checked: false, id: "middle-eastern" },
            { name: "Indian", value: "2", checked: false, id: "indian" },
            { name: "White", value: "0", checked: false, id: "white" },
          ],
        },
        gender: {
          filterName: "gender",
          filterData: [
            { name: "Female", value: "2", checked: false, id: "female" },
            { name: "Male", value: "1", checked: false, id: "male" },
          ],
        },
        charts: {
          filterName: "charts",
          filterData: [
            { name: "Emotions", value: "0", checked: false, id: "emotions" },
            { name: "Attention", value: "1", checked: false, id: "attention" },
          ],
        },
      },

      dateFilterButtons: [
        {
          dateName: "Dy",
          startDate: new Date(),
          endDate: new Date(),
          selected: false,
        },
        {
          dateName: "Wk",
          startDate: new Date(Date.now() - (new Date().getDay() - 1) * 24 * 60 * 60 * 1000),
          endDate: new Date(),
          selected: false,
        },
        {
          dateName: "Mo",
          startDate: new Date(Date.now() - (new Date().getDate() - 1) * 24 * 60 * 60 * 1000),
          endDate: new Date(),
          selected: false,
        },
        {
          dateName: "Last 30 Days",
          startDate: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000),
          endDate: new Date(),
          selected: false,
        },
        {
          dateName: "Last 90 Days",
          startDate: new Date(Date.now() - 130 * 24 * 60 * 60 * 1000),
          endDate: new Date(),
          selected: false,
        },
      ],

      requestData: {
        age: ["0-20", "21-35", "36-50", "51-1000"],
        ethnicity: [0, 1, 2, 3, 4, 5],
        gender: [1, 2],
        charts: [],
        startDate: new Date(),
        endDate: new Date(),
      },

      defaultRequestData: {
        age: ["0-20", "21-35", "36-50", "51-1000"],
        ethnicity: [0, 1, 2, 3, 4, 5],
        gender: [1, 2],
        charts: [],
        startDate: new Date(),
        endDate: new Date(),
      },
    };
  },

  async mounted() {
    document.addEventListener("keydown", (e) => {
      e.keyCode === 27 ? this.closeModal() : false;
    });

    try {
      let charts = {};

      // init charts
      charts.speakingChartModal = this.createChart(
        "speaking-chart-modal",
        mainStore.speakingChartModal.value
      );
      charts.userEmotionsChartModal = this.createChart(
        "user-emotions-chart-modal",
        mainStore.userEmotionsChartModal.value
      );
      charts.engagementChartModal = this.createChart(
        "engagement-chart-modal",
        mainStore.engagementChartModal.value
      );
      charts.attentionsChartModal = this.createChart(
        "attention-chart-modal",
        mainStore.attentionsChartModal.value
      );
      charts.emotionsChartModal = this.createChart(
        "emotions-chart-modal",
        mainStore.emotionsChartModal.value
      );
      charts.speakingChart = this.createChart("speaking-chart", mainStore.speakingChart.value);
      charts.userEmotionsChart = this.createChart(
        "user-emotions-chart",
        mainStore.userEmotionsChart.value
      );
      charts.engagementChart = this.createChart(
        "engagement-chart",
        mainStore.engagementChart.value
      );
      charts.attentionsChart = this.createChart("attention-chart", mainStore.attentionsChart.value);
      charts.emotionsChart = this.createChart("emotions-chart", mainStore.emotionsChart.value);

      charts.multAttentionChart = this.createChart(
        "mult-attention-chart",
        mainStore.multAttentionChart.value
      );
      charts.multAttentionModal = this.createChart(
        "mult-attention-modal",
        mainStore.multAttentionModal.value
      );
      charts.multEmotionsChart = this.createChart(
        "mult-emotions-chart",
        mainStore.multEmotionsChart.value
      );
      charts.multEmotionsModal = this.createChart(
        "mult-emotions-modal",
        mainStore.multEmotionsModal.value
      );

      await mainStore.init();
      // update charts if data will changed
      for (let key of [
        "speakingChart",
        "userEmotionsChart",
        "engagementChart",
        "attentionsChart",
        "emotionsChart",
        "speakingChartModal",
        "userEmotionsChartModal",
        "engagementChartModal",
        "attentionsChartModal",
        "emotionsChartModal",
        "multAttentionChart",
        "multAttentionModal",
        "multEmotionsChart",
        "multEmotionsModal",
      ]) {
        watch(mainStore[key], () => {
          charts[key].config.data = mainStore[key].value.data;
          charts[key].config.options = mainStore[key].value.options;
          charts[key].update();
        });
      }
    } catch (e) {
      this.error = e.message;
    }
  },

  unmounted() {
    document.removeEventListener("keydown", (e) => {
      e.keyCode === 27 ? this.closeModal() : false;
    });
  },
};
</script>
