<template>
  <div class="input-block">
    <input
      :type="type"
      @change="isSelected(filter, checked, shouldDataUpdate)"
      :id="inputId"
      :checked="checked"
    />
    <label :for="inputId">{{ inputName }}</label>
  </div>
</template>

<script>
export default {
  name: "v-input",
  props: {
    type: {
      type: String,
      default: "",
    },
    inputValue: {
      type: String,
      default: "",
    },
    inputName: {
      type: String,
      default: "",
    },
    inputId: {
      type: String,
      default: "",
    },
    filterName: {
      type: String,
      default: "",
    },
    filter: {
      type: Object,
      default: () => {},
    },
    checked: {
      type: Boolean,
      default: false,
    },
    shouldDataUpdate: {
      type: Boolean,
      default: false,
    },
  },

  methods: {
    isSelected(filter, checked, shouldDataUpdate) {
      this.$emit("set-checked", checked, filter, shouldDataUpdate);
    },
  },
};
</script>
