import axios from "axios";
// eslint-disable-next-line no-unused-vars
import utils from "@/system/utils";
// eslint-disable-next-line no-unused-vars
import {
  emotionsBySession,
  mockSessions,
  participationsBySession,
  attentionsBySession,
  participantsBySession,
  aggregatedData,
} from "@/system/mockData";
import { BASE_URL, IS_LOCAL_MODE } from "@/system/config";

const axiosInstant = axios.create({
  baseURL: BASE_URL,
  withCredentials: true,
});

export default {
  /**
   * get session[sessionId] participants
   *
   * @param sessionId
   * @returns {Promise<[{participantId,name,dateCreated,note,thumbnail,ethnicity,detectedEthnicity,age,detectedAge,gender,detectedGender,speakingTime,averageEmotions,averageAttention}]>}
   */
  async getSessionParticipants(sessionId) {
    let res = await axios.get(`${BASE_URL}/session/participants?sessionId=${sessionId}`);

    return res.data.data;
  },
  /**
   * get session[sessionId] emotions
   *
   * @param sessionId
   * @returns {Promise<[{sessionEventId,participantId,timestamp,event,surprised,scared,disgust,happy,sad,angry,neutral}]>}
   */
  async getSessionEmotions(sessionId) {
    let res = await axios.get(`${BASE_URL}/session/emotions?sessionId=${sessionId}`);

    return res.data.data;
  },
  /**
   *
   * @param sessionId
   * @returns {Promise<*>}
   */
  async getSessionAttentions(sessionId) {
    let res = await axios.get(`${BASE_URL}/session/attentions?sessionId=${sessionId}`);

    return res.data.data;
  },

  async getSessionParticipations(sessionId) {
    let res = await axios.get(`${BASE_URL}/session/participations?sessionId=${sessionId}`);

    return res.data.data;
  },

  async getAllSessions() {
    if (!IS_LOCAL_MODE) return (await axiosInstant.get(`/session/all`), {}).data;

    await utils.promiseTimeout(500);

    return mockSessions;
  },
  /**
   * @param sessionId
   * @returns {Promise<{participations: [], emotions: [], attentions: [], participants: []}>}
   */
  async getSessionData(sessionId) {
    if (IS_LOCAL_MODE) await utils.promiseTimeout(2000);

    let participations = [];
    if (IS_LOCAL_MODE) {
      if (participationsBySession[sessionId])
        participations = participationsBySession[sessionId].data;
    } else participations = await this.getSessionParticipations(sessionId);

    let participants = [];
    if (IS_LOCAL_MODE) {
      if (participantsBySession[sessionId]) participants = participantsBySession[sessionId].data;
    } else {
      participants = await this.getSessionParticipants(sessionId);
    }
    // update participant css-class for colored frame
    participants.map((p, key) => (p.colorClass = "avatar-color-" + key));

    let attentions = [];
    if (IS_LOCAL_MODE) {
      if (attentionsBySession[sessionId]) attentions = attentionsBySession[sessionId].data;
    } else {
      attentions = await this.getSessionAttentions(sessionId);
    }

    let emotions = [];
    if (IS_LOCAL_MODE) {
      if (emotionsBySession[sessionId]) emotions = emotionsBySession[sessionId].data;
    } else {
      emotions = await this.getSessionEmotions(sessionId);
    }

    return {
      participations,
      attentions,
      participants,
      emotions,
    };
  },

  async getMultSessionsData(params) {
    let data = {
      multParticipations: {},
      multParticipants: {},
      multAttentions: {},
      multEmotions: {},
    };

    if (IS_LOCAL_MODE) {
      await utils.promiseTimeout(2000);
      data = aggregatedData;
    } else {
      let ageUrlsArr = [];
      let ethnicityUrlsArrs = [];
      let fullUrlsArr = [];
      let startDate = new Date(...params.startDate).getTime() / 1000;
      let endDate = new Date(...params.endDate).getTime() / 1000;
      params.age.forEach((item) => {
        let url = BASE_URL + "/session/aggregated?" + `from=${startDate}&to=${endDate}&`;
        let ageArr = item.split("-").map((item) => +item);
        console.log(ageArr);
        url += `ageFrom=${ageArr[0]}&ageTo=${ageArr[1]}&`;
        ageUrlsArr.push(url);
      });

      params.ethnicity.forEach((item) => {
        ageUrlsArr.forEach((url) => {
          url += `ethnicity=${item}&`;
          ethnicityUrlsArrs.push(url);
        });
      });

      params.gender.forEach((item) => {
        ethnicityUrlsArrs.forEach((url) => {
          url += `gender=${item}`;
          fullUrlsArr.push(url);
        });
      });

      console.log(fullUrlsArr);

      data = [];
      async function processArray() {
        let newData = [];
        for (let url of fullUrlsArr) {
          let res = await axios.get(url);
          newData.push(res.data);
        }
        return newData;
      }
      data = await processArray();
      console.log(data);
    }
    return data;
  },
};
