import { createRouter, createWebHashHistory } from 'vue-router'
import Layout from "@/views/Layout";
import Charts from "@/views/charts/Charts";

const routes = [
  {
    path: "/",
    component: Layout,
    redirect: "/main",

    children: [
      {
        path: "main",
        name: "main",
        component: Charts
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
